/**
 * Data validation storage utilities
 */

export class DataValidationStorage {
  public dbName: string;
  private storage: Map<string, Map<any, any>>;

  constructor(dbName: string) {
    this.dbName = dbName;
    this.storage = new Map();
  }

  isValid(data: any): boolean {
    if (data === null || data === undefined) {
      return false;
    }

    if (typeof data === "string" && data.length === 0) {
      return false;
    }

    if (typeof data === "object" && Object.keys(data).length === 0) {
      return true; // Empty objects are valid
    }

    return true;
  }

  async store(collection: string, data: any): Promise<void> {
    if (!this.isValid(data)) {
      throw new Error("Invalid data");
    }

    if (!this.storage.has(collection)) {
      this.storage.set(collection, new Map());
    }

    const col = this.storage.get(collection)!;
    const id = data.id || Date.now();
    col.set(id, { ...data, id });
  }

  async get(collection: string, id: any): Promise<any> {
    const col = this.storage.get(collection);
    if (!col) {
      return null;
    }

    return col.get(id) || null;
  }

  async getAll(collection: string): Promise<any[]> {
    const col = this.storage.get(collection);
    if (!col) {
      return [];
    }

    return Array.from(col.values());
  }

  async update(collection: string, id: any, data: any): Promise<void> {
    if (!this.isValid(data)) {
      throw new Error("Invalid data");
    }

    const col = this.storage.get(collection);
    if (!col || !col.has(id)) {
      throw new Error("Item not found");
    }

    col.set(id, { ...data, id });
  }

  async delete(collection: string, id: any): Promise<void> {
    const col = this.storage.get(collection);
    if (!col) {
      return;
    }

    col.delete(id);
  }

  async clear(collection?: string): Promise<void> {
    if (collection) {
      this.storage.delete(collection);
    } else {
      this.storage.clear();
    }
  }

  async count(collection: string): Promise<number> {
    const col = this.storage.get(collection);
    return col ? col.size : 0;
  }
}
